/*COLOR*/
@orange: #f39c12;
@orangeH: #f5a916;
@orangeP: #f1900f;

@xxlGrey: #f2f2f2;
@xlGrey: #eaeaea;
@lGrey: #c0c0c0;
@sGrey: #595959;
@grey: #303030;
@green: #52b636;
@greenH: #5dc03f;
@greenP: #47ac2d;
@red: #e43636;
@redH: #ee4747;
@redP: #da2525;
/*FONTS*/
h2, .h2 {
  font-size: 24px;
  color: @grey;
}
h3, .h3 {
  font-size: 20px;
  color: @grey;
}
.title {
  padding-top: 15px;
  padding-bottom: 12px;
  font-size: 18px;
  line-height: 30px;
  color: @grey;
  text-transform: uppercase;
  border-bottom: solid 2px @xlGrey;
  letter-spacing: .2em;
  margin-bottom: 10px;
  span {
    border-bottom: solid 2px @orange;
    padding-bottom: 16px;
  }
}
.text-center {
  text-align: center;
}
.link {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  &_dotted {
    border-bottom: dotted 1px @grey;
  }
  &_s {
    font-size: 13px;
  }
  &_h_orange {
    &:hover {
      color: @orange;
    }
    &:active {
      color: @orangeP;
    }
  }
  &_new {
    color: @green;
    &:hover {
      color: @greenH;
    }
    &:active {
      color: @greenP;
    }
  }
  &_sales {
    color: @red;
    &:hover {
      color: @redH;
    }
    &:active {
      color: @redP;
    }
  }
}
.btn {
  text-decoration: none;
  color: #fff;
  border-radius: 3px;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  &_L {
    font-size: 12px;
    text-transform: uppercase;
    padding: 11px 34px;
    letter-spacing: .1em;
  }
  &_orange {
    background-color: @orange;
    &:hover {
      background-color: @orange;
    }
    &:active {
      background-color: @orangeP;
    }
  }
  &_black {
    background-color: @grey;
    &:hover {
      background-color: @orange;
    }
    &:active {
      background-color: @orangeP;
    }
  }
  &_sales {
    background-color: @red;
    &:hover {
      background-color: @redH;
    }
    &:active {
      background-color: @redP;
    }
  }
  &_new {
    background-color: @green;
    &:hover {
      background-color: @greenH;
    }
    &:active {
      background-color: @greenP;
    }
  }
}
.uppercase {
  text-transform: uppercase;
}
